import styled, { css } from 'styled-components'
import { deviceLaptop } from '../../css/device'
import { transitionDuration } from '../../css/variables'
import { Inner } from '../Inner'
import { stripeHeight } from '../Stripe/StripeItem'

export const headerHeight = 49
// export const headerHeightExpanded = 320
export const headerHeightTablet = 90
export const shadowClassName = 'shadow'

interface HeaderWrapperProps {
  expanded?: boolean;
}

// type IsExpanded = (normal: number, expanded: number) => ({ expanded }: HeaderWrapperProps) => number;
// const isExpanded: IsExpanded = (normal, expanded) => ({ expanded: ex }) => ex ? expanded : normal

const shadowCss = css`
  box-shadow: 0 3px 5px rgba(57, 63, 72, .3);
`

export const HeaderWrapper = styled.header`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  z-index: 1;
  transition-duration: ${transitionDuration};
  
  ${({ expanded }: HeaderWrapperProps) => expanded ? shadowCss : css`
    &.${shadowClassName} {
      ${shadowCss};
    }
  `};

  ${deviceLaptop`
    height: ${headerHeightTablet}px;

    ${Inner} {
      height: ${headerHeightTablet - stripeHeight}px;
      display: flex;
    }
  `};
`
