import React, { FC } from 'react'
import { StripeWrapper } from './StripeWrapper'
import { StripeItem } from './StripeItem'

export const stripeColors = ['#188ba0', '#52c1ac', '#f0ca4d', '#f99f3e', '#e15a49']

export const Stripe: FC = () => (
  <StripeWrapper>
    {stripeColors.map(color => (
      <StripeItem key={color} color={color} />
    ))}
  </StripeWrapper>
)
