import styled, { css } from 'styled-components'
import { deviceLaptop } from '../../../css/device'
import { ContainerContent } from './ContainerContent'
import { Proposition } from './Proposition'
import bgTexture from '../../../assets/bg-texture.png'

interface ContainerProps {
  invert?: boolean;
  color?: string
}

export const Container = styled.div`
  padding: .5em;
  min-height: 10em;
  position: relative;
  
  ${({ invert, color }: ContainerProps) => invert ?
    css`
      background-color: ${color};
      background-image: url(${bgTexture});
    
      h2, p, ul {
        color: #fff;
      }
    ` :
    css`
      background-color: #fff
    `
  }; 
  
  ${deviceLaptop`
    h2 {
      width: 100%;
    }
    
    ${ContainerContent} {
      ${css`
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        
        ${Proposition} {
          ${css`
            padding: 2em;
            margin: 2em 0;
            width: 100%;
            flex: 0 0 50%;
            
            &:nth-last-child(odd) {
              margin: 2em auto;
            }
          `};
        }
      `};
    }
  `};
`
