import React, { FC, useState, createRef, useEffect } from 'react'
import { HeaderWrapper, shadowClassName } from './HeaderWrapper'
import { Logo } from '../Logo/Logo'
import { PhoneNumber, PhoneNumberWrapper } from './PhoneNumber'
import { MenuIcon } from '../MenuIcon/MenuIcon'
import { Inner } from '../Inner'
import { Navigation } from '../Navigation/Navigation'
import { Stripe } from '../Stripe/Stripe'
import { HeaderInnerWrapper } from './HeaderInnerWrapper'
import { i18n } from '../../i18n/i18n'
import { getPhoneNumber } from '../../helpers/getPhoneNumber'

const ref = createRef<HTMLDivElement>()

const handleScroll = () => {
  if (ref.current) {
    if (window.scrollY !== 0) {
      ref.current.classList.add(shadowClassName)
    } else {
      ref.current.classList.remove(shadowClassName)
    }
  }
}

export const Header: FC = () => {
  const [menuOpened, toggleMenuOpened] = useState(false)
  const toggleMenu = () => toggleMenuOpened(!menuOpened)

  useEffect(() => {
    window.addEventListener('scroll', handleScroll, true)
    handleScroll()
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <HeaderWrapper ref={ref} expanded={menuOpened}>
      <Stripe />
      <Inner>
        <HeaderInnerWrapper>
          <Logo>
            {i18n('airina')}
          </Logo>
          <PhoneNumberWrapper>
            <PhoneNumber href={getPhoneNumber(true)}>
              {getPhoneNumber()}
            </PhoneNumber>
          </PhoneNumberWrapper>
          <MenuIcon opened={menuOpened} onClick={toggleMenu} />
        </HeaderInnerWrapper>
        <Navigation visible={menuOpened} onClick={toggleMenu} />
      </Inner>
    </HeaderWrapper>
  )
}
