import styled from 'styled-components'
import whatsapp from '../../../assets/whatsapp-2.svg'
import email from '../../../assets/mail-2.svg'
import address from '../../../assets/placeholder-2.svg'
import facebook from '../../../assets/facebook-2.svg'
import phone from '../../../assets/telephone.svg'

type Icons = 'whatsapp' | 'facebook' | 'email' | 'address' | 'phone'

const map: {
  [K in Icons]: string
} = {
  whatsapp,
  facebook,
  email,
  address,
  phone,
}

interface IconProps {
  icon: Icons
}

export const Icon = styled.i`
  display: block;
  width: 32px;
  height: 32px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: ${({ icon }: IconProps) => `url(${map[icon]})`};
`
