import React, { FC, RefObject } from 'react'
import { Nav } from './Nav'
import { A } from './A'
import { i18n } from '../../i18n/i18n'
import { Socials } from '../Footer/Socials/Socials'
import { refs } from '../Content/Content'

interface NavigationProps {
  visible?: boolean;
  footer?: boolean;

  onClick?(): void;
}

export const Navigation: FC<NavigationProps> = ({ visible, footer, onClick: callback }) => {

  const onClick = (ref?: RefObject<HTMLDivElement>) => (e: any /*TODO*/) => {
    e.preventDefault()

    if (ref && ref.current) {
      ref.current.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      })
    }

    if (callback) {
      callback()
    }
  }

  return (
    <Nav visible={visible}>
      <Socials />
      <A onClick={onClick(refs.services)}>{i18n('nav.service')}</A>
      <A onClick={onClick(refs.office)}>{i18n('nav.office')}</A>
      <A onClick={onClick(refs.contacts)}>{i18n('nav.contacts')}</A>
    </Nav>
  )
}

Navigation.defaultProps = {
  visible: false,
  footer: false,
}
