import React, { FC } from 'react'
import styled, { css } from 'styled-components'
import { deviceLaptop } from '../../css/device'
import { hoverBackgroundColor, transitionDuration } from '../../css/variables'

interface LogoProps {
  center?: boolean
}

const A = styled.a`
  transition-duration: ${transitionDuration};
  text-decoration: none;
  color: inherit;
  display: flex;
  align-items: center;
 
  &:hover {
    background-color: ${hoverBackgroundColor};
  }
  
  ${({ center }: LogoProps) => center && css`
    text-align: center;
    display: inline-block;
    margin: 0 auto;
  `};
`

export const Heading = styled.h1`
  padding: 0 .5em;
  margin: 0;
  
  ${deviceLaptop`
    padding: 0 .1em;
    font-size: 3em;
  `};
`

export const Logo: FC<LogoProps> = ({ center, children }) => (
  <A href="/" center={center}>
    <Heading>{children}</Heading>
  </A>
)
