import styled, { css } from 'styled-components'
import { Bar } from './Bar'
import { stripeColors } from '../Stripe/Stripe'
import { deviceLaptop } from '../../css/device'
import { hoverBackgroundColor } from '../../css/variables'

interface MenuIconWrapperProps {
  opened?: boolean
}

const barStyles = css`
  ${Bar} {
    background-color: ${stripeColors[4]};
    &:first-child {
      -webkit-transform: rotate(-45deg) translate(-.563em, .375em);
      transform: rotate(-45deg) translate(-.563em, .375em);
    }
    &:nth-child(2) {
      opacity: 0;
    }
    &:nth-child(3) {
      -webkit-transform: rotate(45deg) translate(-.5em, -.5em);
      transform: rotate(45deg) translate(-.5em, -.5em);
    }
  }
`

export const MenuIconWrapper = styled.div`
  display: inline-block;
  cursor: pointer;
  ${({ opened }: MenuIconWrapperProps) => opened && barStyles};
  font-size: 14px;
  padding: .4em .4em 0;
  
  ${deviceLaptop`
    display: none;
  `};
  
  &:hover {
    background-color: ${hoverBackgroundColor};
  }
`
