import { css } from 'styled-components'
import 'array-from-polyfill'

const size = {
  mobileS: '320px',
  mobileM: '375px',
  mobileL: '425px',
  tablet: '768px',
  laptop: '1024px',
  laptopL: '1440px',
  desktop: '2560px',
}

export const device = {
  mobileS: `(min-width: ${size.mobileS})`,
  mobileM: `(min-width: ${size.mobileM})`,
  mobileL: `(min-width: ${size.mobileL})`,
  tablet: `(min-width: ${size.tablet})`,
  laptop: `(min-width: ${size.laptop})`,
  laptopL: `(min-width: ${size.laptopL})`,
  desktop: `(min-width: ${size.desktop})`,
  desktopL: `(min-width: ${size.desktop})`,
}

const merge = (a1: TemplateStringsArray, a2: any[]) => {
  const l = Math.min(a1.length, a2.length)
  // @ts-ignore
  return [].concat(...Array.from({ length: l }, (_, i) => [a1[i], a2[i]]), a1.slice(l), a2.slice(l))
}

export const deviceLaptop = (styles: TemplateStringsArray, ...args: any[]) => css`
  @media ${device.tablet} {
    ${merge(styles, args)};
  }
`
